.serviceDetail {
	@media (max-width: 1024.98px) {
		padding-bottom: 55px;
		padding-top: 55px;
	}
	@media (max-width: 767.98px) {
		padding-bottom: 30px;
		padding-top: 30px;
	}
}

.serviceDetail__img {
	width: 100%;
}

.serviceDetail__content {
	padding-top: 3rem;
	p,
	ul,
	ol {
		font-weight: 300;
		color: var(--white);
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
	ul,
	ol {
		padding-left: 20px;
	}
	.swiper-pagination-bullets.swiper-pagination--custom {
		text-align: center;
		width: 100%;
		position: static;
		margin-top: 2rem;
		.swiper-pagination-bullet {
			width: 3.5rem;
			border-radius: 0.9rem;
			background-color: rgba(255, 255, 255, 0.5);
			&-active {
				background-color: rgba(255, 255, 255, 1);
			}
		}
	}
	.serviceItem__title {
		position: static;
		transform: translateY(0);
		margin-bottom: 2rem;
		color: var(--white);
		&:before {
			background-color: var(--white);
		}
	}
	.serviceDetail__subTitle {
		font-size: 2rem;
		font-weight: 500;
		margin-bottom: 2rem;
	}
	.swiper-container {
		height: 100%;
	}
	@media (max-width: 1024.98px) {
		-ms-flex: 0 0 100%;
		flex: 0 0 100%;
		max-width: 100%;
		padding: 25px 0;
		// background-color: rgba(0, 0, 0, 0.8);
		.swiper-container {
			height: auto;
		}
		p,
		ul,
		ol,.serviceItem__title {
			color: var(--black);
		}
		.swiper-pagination-bullets.swiper-pagination--custom {
			display: none;
		}
	}
}
@media (min-width: 1025px) {
	.serviceDetail {
		.container-fluid {
			padding-left: 0;
			padding-right: 0;
		}
	}
	.serviceDetail__img {
		height: 100vh;
		width: 100%;
		.swiper-container {
			height: 100%;
		}
	}
	.serviceDetail__content {
		position: absolute;
		width: 50%;
		height: 100%;
		left: 0;
		top: 0;
		background-color: rgba(0, 0, 0, 0.8);
		z-index: 10;
	}
	.serviceDetail__contentWrapper {
		padding: 0 60px;
		overflow-y: auto;
		max-height: 100%;
	}
}
@media (min-width: 1200px) {
	.serviceDetail__content {
		padding: 70px 0;
		.swiper-pagination-bullets.swiper-pagination--custom {
			position: absolute;
			bottom:20px;
		}
	}
	.serviceDetail__contentWrapper {
		padding: 0 100px;
	}
}
