// Index section 1 - Banner
.indexBanner {
  padding-top: 80px;
  height: 800px;
  overflow: hidden;
  .swiper-container,
  .swiper-wrapper,
  .swiper-slide {
    height: 100%;
    position: relative;
  }
  .swiper-slide {
    overflow: hidden;
  }
  .banner__content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #fff;
    padding: 24px;
    display: none;
    .btn {
      height: 5.2rem;
      font-size: 1.8rem;
      color: var(--ink-70);
      margin-top: 4rem;
      padding-left: 2.5rem;
      padding-right: 2.5rem;
      @media (min-width: 1025px) {
        margin-top: 8rem;
        &:hover {
          background-color: var(--ink-70);
          color: var(--white);
        }
      }
    }
  }
  .banner__title {
    font-family: var(--font-main);
    font-size: 3rem;
    @media (min-width: 1025px) {
      font-size: calc(48 / 1920) * 100vw;
    }
  }
  .banner__subTitle {
    font-size: 1.8rem;
    margin-top: 2rem;
    color: var(--ink-70);
  }
  .banner__description {
    max-width: 590px;
    width: 100%;
    margin-top: 4rem;
    margin-left: auto;
    margin-right: auto;
    font-size: 1.6rem;
    font-style: italic;
    color: var(--ink-70);
    @media (min-width: 1025px) {
      margin-top: 8rem;
    }
  }
  .banner__author {
    font-size: 1.4rem;
    color: var(--ink-70);
    margin-top: 1rem;
  }
  .banner__img {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: left;
    // opacity: 0.8;
    // animation: zoomInOut 10s linear infinite forwards;
  }
  .swiper-pagination {
    left: 0;
    right: 0;
    margin: auto 0;
    bottom: 1rem;
    .swiper-pagination-bullet {
      width: 1rem;
      height: 1rem;
    }
    .swiper-pagination-bullet-active {
      background-color: #db4437;
    }
  }
  @media (max-width: 1025px) {
    padding-top: 0;
    height: 30vh;
  }
}

// Index section 2 - Introduce
.service-index {
  .serviceItem {
    display: block;
    position: relative;
    border-top: 4px solid #db4437;
    border-bottom: 4px solid #db4437;
    @media (min-width: 768px) {
      max-height: auto;
    }
    @media (min-width: 1200px) {
      max-height: 600px;
    }
    @media (max-width: 480px) {
      border: unset;
    }
    .serviceItem__content {
      .serviceItem__title {
        font-weight: 400 !important;
        font-size: 2.2rem !important;
      }
    }
    .serviceItem__description {
      font-size: 14px;
    }
    &:hover {
      .serviceItem__img {
        &:before {
          border: unset;
        }
      }
    }
  }
}
.indexIntroduce__icon {
  width: 50px;
  height: 50px;
  margin: auto;
  @media (min-width: 768px) {
    width: 75px;
    height: 75px;
  }
  @media (min-width: 1200px) {
    width: 100px;
    height: 100px;
  }
}
.indexIntroduce__title {
  margin-top: 4rem;
  margin-bottom: 2rem;
  color: #fff;
  background-color: #db4437;
  padding: 10px 20px;
  width: fit-content;
  @media (max-width: 1025px) {
    margin-top: 2rem;
    text-align: center;
  }
  @media (max-width: 480px) {
    margin-bottom: 3rem;
  }
}
.indexIntroduce__content {
  padding: 0 25px;
  @media (max-width: 480px) {
    margin-bottom: 30px;
  }
}

.indexIntroduce__description {
  margin-top: 1.5rem;
  font-size: 15px;
  line-height: 18px;
  color: #373737;
  font-weight: 400;
  @media (max-width: 1025px) {
    margin-top: 2rem;
    text-align: justify;
  }
  a {
    font-size: 16px;
    line-height: 18px;
    color: #4d4d4d;
    font-weight: 700;
  }
  @media (max-width: 1025px) {
    margin-top: 1rem;
    text-align: justify;
  }
  &:hover {
    a {
      color: #db4437;
    }
  }
  &:last-child {
    margin-top: 2.5rem;
  }
}

.indexIntroduce__img {
  @include img(calc(1 / 1));
  margin-top: 4rem;
  @media (min-width: 768px) {
    margin-top: 0;
    &:before {
      content: "";
      position: absolute;
      width: 60%;
      height: 100%;
      left: -15px;
      bottom: -25px;
      background-color: #0a1439;
    }
  }
  @media (min-width: 1025px) {
    &:before {
      left: -30px;
      bottom: -45px;
    }
  }
}
.indexIntroduce__buttons {
  margin-top: 4rem;
  text-align: right;
  padding: 0 25px;
  .btn {
    font-size: 14px;
    border: 1px solid #db4437;
    color: #db4437;
    &:hover {
      background-color: #db4437;
      color: #fff;
      border: 1px solid #db4437;
    }
  }
  @media (max-width: 480px) {
    display: none;
  }
}

// Index section 3 - Video
.indexVideo {
  .row--float {
    position: relative;
  }
}
.indexVideo__img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: 6px solid #db4437;
  padding: 5px;
  a {
    background-color: var(--black);
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    @include img(calc(600 / 960));
    img {
      z-index: 1;
      transition: opacity 0.3s ease;
    }
    &:before {
      font-family: "Font Awesome 5 Pro";
      font-weight: 300;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      display: inline-block;
      font-style: normal;
      font-variant: normal;
      text-rendering: auto;
      line-height: 1;
      content: "\f144";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 50px;
      z-index: 5;
      color: var(--main);
    }
    @media (min-width: 768px) {
      &:before {
        font-size: 70px;
      }
    }
    @media (min-width: 1025px) {
      &:hover {
        img {
          opacity: 0.3;
        }
      }
    }
    @media (min-width: 1200px) {
      &:before {
        font-size: 90px;
      }
    }
  }
  @media (min-width: 768px) {
    width: calc(100% / 12 * 6.5);
  }
  @media (min-width: 1440px) {
    margin-left: 0;
  }
}
.indexVideo__title {
  color: #fff;
  background-color: #db4437;
  width: fit-content;
  padding: 10px 20px;
}
.indexVideo__content {
  margin-top: 4rem;
  @media (min-width: 1440px) {
    margin-top: 0;
    padding: 55px 70px;
    background-color: rgba(255, 255, 255, 0.8);
    position: absolute;
    left: 46%;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    z-index: 5;
    // box-shadow: var(--shadow-2);
  }
  @media (max-width: 1025px) {
    text-align: justify;
  }
}
.indexVideo__description {
  margin-top: 10px;
  font-size: 15px;
  ~ .indexVideo__description {
    margin-top: 15px;
  }
}

// Index section 4 - Hot Service
.indexHotService {
  .swiper-container {
    overflow: hidden;
  }
  .section__title {
    padding: 10px 20px;
    color: #db4437;
    width: fit-content;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      background-color: #db4437;
      width: 80px;
      height: 2px;
      top: 50%;
      transform: translateY(-50%);
      right: -70px;
    }
    &::before {
      content: "";
      position: absolute;
      background-color: #db4437;
      width: 80px;
      height: 2px;
      top: 50%;
      transform: translateY(-50%);
      left: -70px;
    }
    @media (max-width: 1024px) {
      &::after,
      &::before {
        display: none;
      }
    }
  }
}
.indexHotServiceItem__img {
  background-color: var(--black);
  overflow: hidden;
  transition: all 0.3s;
  img {
    opacity: 0.6;
    transition: all 0.3s ease;
    transform: scale(1);
  }
}
.indexHotServiceItem__content {
  position: absolute;
  left: 0;
  bottom: 15px;
  width: 100%;
  padding: 10px;
  z-index: 5;
  transition: all 0.3s ease;
  h4 {
    color: var(--white);
    font-size: 2rem;
    font-weight: 500;
    &:first-child {
      font-size: 1.3rem;
      margin-bottom: 10px;
    }
  }
  @media (min-width: 320px) {
    h4 {
      font-size: 1.6rem;
    }
  }
  @media (min-width: 768px) {
    padding: 20px 20px 20px 20px;
    bottom: 0px;
    h4 {
      font-size: 1.7rem;
    }
  }
  @media (min-width: 1200px) {
    padding: 0 50px 050px 50px;
  }
}
.indexHotServiceItem__buttons {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: all 0.3s ease;
  opacity: 0;
  visibility: hidden;
  z-index: 4;
  @media (max-width: 480px) {
    display: none;
  }
  @media (max-width: 1024.98px) {
    left: 15px;
    top: 15px;
    right: 15px;
    bottom: 15px;
    opacity: 1;
    visibility: visible;
  }
}
.indexHotServiceItem {
  position: relative;
  display: block;
  @media (min-width: 1025px) {
    &:hover {
      .indexHotServiceItem__buttons {
        left: 30px;
        top: 30px;
        right: 30px;
        bottom: 30px;
        opacity: 1;
        visibility: visible;
      }
      .indexHotServiceItem__content {
        // opacity: 0.6;
        // visibility: hidden;
        transform: translateY(10px);
      }
      .indexHotServiceItem__img {
        background-color: #db4437;
        img {
          opacity: 0.65;
          transform: scale(1.1);
        }
      }
    }
  }
}

// Index section 5 - Customer
.indexCustomer {
  .section__blockTitle {
    flex-direction: column;
    align-items: center;
  }
  .swiper-container {
    border-top: 2px solid #db4437;
    border-bottom: 2px solid #db4437;
    padding: 15px 0;
    .swiper-wrapper {
      @media (min-width: 1025px) {
        height: 300px;
      }
      @media (min-width: 1200px) {
        height: 350px;
      }
    }
    .swiper-pagination {
      position: static;
      display: flex;
      justify-content: center;
      margin-top: 2rem;
      .swiper-pagination-bullet {
        @media (max-width: 1025px) {
          width: 0.8rem;
          height: 0.8rem;
        }
      }
    }
  }
  .swiper-slide {
    height: 50%;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      padding: 0 15px;
      @media (max-width: 480px) {
        padding: 5px;
      }
      img {
        height: unset;
      }
    }
  }
}
.indexCustomer__title {
  font-size: 10rem;
  line-height: 120px;
  font-weight: 700;
  color: #db4437;
}
.indexCustomer__subTitle {
  font-size: 2.8rem;
  font-weight: 700;
  color: #0a1439;
  @media (max-width: 1025px) {
    text-align: center;
  }
  @media (max-width: 480px) {
    font-size: 2.2rem;
  }
}

.afbe {
  .swiper-button-prev,
  .swiper-button-next {
    z-index: 999;
  }
  h2 {
    margin-top: 20px;
  }
}

.afbe .swiper-slide {
  padding: 0 4%;
}

.comparison {
  width: 100%;
  padding-bottom: 100%;
  overflow: hidden;
  position: relative;
  max-height: 300px;
}

figure {
  position: absolute;
  background-size: cover;
  font-size: 0;
  width: 100%;
  height: 100%;
  margin: 0;
}

#divisor {
  background-size: cover;
  position: absolute;
  width: 50%;
  box-shadow: 0 5px 10px -2px rgba(0, 0, 0, 0.3);
  bottom: 0;
  height: 100%;

  &::before,
  &::after {
    content: "";
    position: absolute;
    right: -2px;
    width: 4px;
    height: calc(50% - 25px);
    background: white;
    z-index: 3;
  }
  &::before {
    top: 0;
    box-shadow: 0 -3px 8px 1px rgba(0, 0, 0, 0.3);
  }
  &::after {
    bottom: 0;
    box-shadow: 0 3px 8px 1px rgba(0, 0, 0, 0.3);
  }
}
#handle {
  position: absolute;
  height: 50px;
  width: 50px;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  z-index: 1;

  &::before,
  &::after {
    content: "";
    width: 0;
    height: 0;
    border: 6px inset transparent;
    position: absolute;
    top: 50%;
    margin-top: -6px;
  }
  &::before {
    border-right: 6px solid white;
    left: 50%;
    margin-left: -17px;
  }
  &::after {
    border-left: 6px solid white;
    right: 50%;
    margin-right: -17px;
  }
}

input[type="range"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  position: absolute;
  top: 50%;
  left: -25px;
  transform: translateY(-50%);
  background-color: transparent;
  width: calc(100% + 50px);
  z-index: 2;
  cursor: pointer;

  &:focus,
  &:active {
    border: none;
    outline: none;
  }
}
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background: transparent;
  border: 4px solid white;
  box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.3);
}
input[type="range"]::-moz-range-track {
  -moz-appearance: none;
  height: 15px;
  width: 100%;
  background-color: transparent;
  position: relative;
  outline: none;
}

// section 6
.indexNews {
  h2.section__title {
    color: #db4437;
    width: fit-content;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      width: 40%;
      height: 2px;
      background-color: #db4437;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .newsItem {
    text-decoration: unset;
  }
}

//section 7
.indexWhyChoice {
  h2.section__title {
    text-align: center;
    padding: 10px 20px;
    color: #fff;
    background-color: #db4437;
    width: fit-content;
    left: 0;
    right: 0;
    margin: 0 auto;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      width: 80%;
      height: 2px;
      background-color: #db4437;
      bottom: -10px;
      left: 0;
      right: 0;
      margin: 0 auto;
    }
    &:before {
      content: "";
      position: absolute;
      width: 80%;
      height: 2px;
      background-color: #db4437;
      top: -10px;
      left: 0;
      right: 0;
      margin: 0 auto;
    }
  }
  .item-why {
    text-align: center;
  }
  .why-list {
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(3, 1.2fr);
    column-gap: 20px;
    row-gap: 20px;
    .item-why {
      padding: 25px;
      border: 1px solid rgba($color: #db4437, $alpha: 0.3);
      border-bottom: 10px solid rgba($color: #db4437, $alpha: 0.3);
      border-radius: 12px;
      transition: all 0.3s;
      &:hover {
        border: 1px solid rgba($color: #db4437, $alpha: 1);
        border-bottom: 10px solid rgba($color: #db4437, $alpha: 1);
      }
      .icon {
        height: 80px;
      }
      h3 {
        font-size: 2.5rem;
        line-height: 18px;
        color: #db4437;
        font-weight: 700;
        margin: 0 0 1.5rem 0;
        text-transform: none;
      }
      .description {
        font-size: 1.6rem;
        color: #373737;
      }
      @media (max-width: 480px) {
        padding: 25px;
        &:first-child {
          padding-top: 0;
        }
        &:last-child {
          padding-bottom: 0;
        }
      }
    }
  }
  @media (max-width: 768px) {
    h2.section__title {
      font-size: 2.2rem;
    }
    .why-list {
      grid-template-columns: repeat(2, 1.2fr);
      margin-top: 30px;
      padding: 0 10px;
      .item-why {
        padding: 10px !important;
        h3.section__title {
          font-size: 1.8rem;
        }
        .description {
          font-size: 1.3rem;
        }
      }
    }
  }
}
