.aboutViewPdfItem__img {
  @include img(calc(375 / 465));
}
.aboutViewPdfItem__viewDetail {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  font-size: 1.8rem;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: all 0.3s ease;
  color: var(--white);
  .fas {
    margin-right: 10px;
  }
}
.aboutViewPdfItem__content {
  padding: 10px 15px;
}
.aboutViewPdfItem__title {
  font-size: 1.8rem;
  color: var(--ink-70);
}
.aboutViewPdfItem {
  margin-bottom: 30px;
  display: block;
  @media (min-width: 1025px) {
    &:hover {
      .aboutViewPdfItem__viewDetail {
        opacity: 1;
      }
    }
  }
}
.aboutTitle-1 {
  .aboutTitle__img {
    background-color: #fff;
    @media (max-width: 568px) {
      min-height: 150px;
    }
  }
  .aboutTitle__content .section__title {
    color: var(--ink-70);
  }
}
