.header--mobile {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 99;
  background-color: var(--white);
  box-shadow: var(--shadow-2);
  .header__logo {
    padding: 11px;
    border-bottom: 0;
    a {
      display: flex;
      align-items: center;
      h1 {
        margin-top: 3px;
        margin-left: 10px;
      }
    }
  }
  .header__toggle {
    margin-left: auto;
    padding: 0 10px;
    font-size: 2.2rem;
  }
  @media (min-width: 1025px) {
    display: none !important;
  }
}

.header {
  position: fixed;
  width: 100%;
  height: 80px;
  display: flex;
  padding: 0 20px;
  justify-content: space-between;
  align-items: center;
  // flex-direction: column;
  background-color: var(--white);
  box-shadow: var(--shadow-1);
  z-index: 100;
  @media (max-width: 1024.98px) {
    opacity: 0;
    transform: translateX(-100%);
    visibility: hidden;
    transition: all 0.3s ease;
    &.active {
      opacity: 1;
      transform: translateX(0);
      visibility: visible;
    }
  }
}
.header__logo {
  img {
    width: 160px;
  }
  h1 {
    font-size: 2rem;
    color: var(--text);
    font-weight: 400;
    margin-top: 10px;
  }
  @media (max-width: 768px) {
    padding: 11px;
    img {
      width: 80px;
    }
  }
  @media (max-width: 568px) {
    img {
      width: 80px;
    }
  }
}
.header__contact {
  padding: 25px;
  border-top: 1px solid rgba(123, 121, 121, 0.25);
  font-weight: 400;
  display: none;
  p {
    font-size: 12px;
    color: var(--ink-50);
  }
  .contact__socials {
    margin-bottom: 15px;
  }
  .contact__item {
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
  .contact__icon {
    width: 20px;
    margin-right: 10px;
    text-align: center;
    display: inline-block;
    font-size: 12px;
  }
  .contact__text {
    font-size: 12px;
    color: var(--ink-50);
    text-decoration: none;
    text-transform: normal;
    &:hover,
    &.active {
      color: var(--main);
    }
    &.active {
      font-weight: 900;
    }
  }
  .social__item {
    font-size: 14px;
    width: 24px;
    height: 24px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: var(--white);
    background: rgba(123, 121, 121, 0.6);
    border-radius: 50%;
    margin-left: 10px;
    @media (min-width: 1025px) {
      &:hover {
        transition: all 0.3s ease;
        background-color: var(--main);
      }
    }
  }
}
.header__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;
  .nav__link {
    font-size: 1.3rem;
    font-family: var(--font-main);
    color: #0a1439;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: left;
    padding-left: 50px;
    width: 100%;
    height: 80px;
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
    @media (min-width: 1025px) {
      transition: all 0.3s ease;
      &:hover,
      &.active {
        color: #db4437;
        // background-color: #0a1439;
      }
    }
  }
  .nav__item {
    &.active {
      .nav__link {
        color: #db4437;
      }
    }
    &--hasSub {
      .nav__sub {
        border-top: 4px solid #db4437;
        transition: all 0.3s ease;
        position: absolute;
        opacity: 0;
        visibility: hidden;
        transform: translateY(15px);
        top: 76px;
        left: 45px;
        width: 180px;
        background-color: var(--white);
        box-shadow: var(--shadow-1);
        overflow-y: auto;
        .nav__link {
          justify-content: flex-start;
          padding: 10px 15px;
          height: unset;
          border-bottom: 1px dashed rgba(123, 121, 121, 0.6);
        }
        > .nav__item {
          &:last-child {
            .nav__link {
              border: unset;
            }
          }
        }
        .nav__back {
          display: none;
        }
        .nav__item {
          &:hover {
            .nav__link {
              background-color: #db4437;
              color: #fff;
            }
          }
        }
      }
      @media (max-width: 1024.98px) {
        .nav__sub {
          left: 0%;
          top: 141px;
          bottom: 164px;
          box-shadow: none;
          .nav__back {
            display: block;
            padding: 12px 10px;
            color: var(--ink-70);
            font-size: 1.6rem;
            text-align: right;
            background-color: var(--ink-30);
            color: var(--white);
            span:nth-child(2) {
              margin-left: 10px;
            }
          }
        }
      }
      @media (max-width: 568px) {
        .nav__sub {
          top: 61px;
        }
      }
      @media (min-width: 1025px) {
        position: relative;
      }
    }
    @media (max-width: 1024.98px) {
      &.active {
        &:hover {
          .nav__sub {
            opacity: 1;
            visibility: visible;
            transform: translateY(0);
          }
        }
      }
    }
    @media (min-width: 1025px) {
      &:hover {
        .nav__sub {
          opacity: 1;
          visibility: visible;
          transform: translateY(0);
        }
      }
    }
    .nav__link {
      span.fal {
        margin-left: 10px;
      }
    }
  }
}

@media (max-width: 1025px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    height: unset;
    width: 200px;
    padding: 0;
    display: flex;
    flex-direction: column;
    background-color: var(--white);
    box-shadow: var(--shadow-1);
    z-index: 100;
    justify-content: unset;
    align-items: unset;
  }
  .header__contact {
    display: block;
  }
  .header__nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: unset;
    text-align: center;
    height: 100%;
    border-top: 1px solid rgba(123, 121, 121, 0.25);
    .nav__link {
      height: unset;
      font-size: 1.3rem;
      font-family: var(--font-main);
      color: #0a1439;
      font-weight: 500;
      display: flex;
      align-items: center;
      justify-content: left;
      padding: 12px 0 12px 50px;
      width: 100%;
      cursor: pointer;
      text-transform: uppercase;
      @media (min-width: 1025px) {
        transition: all 0.3s ease;
        &:hover,
        &.active {
          color: #f4c22b;
          background-color: #0a1439;
        }
      }
    }
    .nav__item {
      &.active {
        .nav__link {
          color: #db4437;
        }
      }
      &--hasSub {
        .nav__sub {
          transition: all 0.3s ease;
          position: absolute;
          opacity: 0;
          visibility: hidden;
          transform: translateY(15px);
          top: 0%;
          left: 100%;
          width: 100%;
          background-color: var(--white);
          box-shadow: var(--shadow-1);
          overflow-y: auto;
          .nav__link {
            justify-content: flex-start;
          }
          .nav__back {
            display: none;
          }
        }
        @media (max-width: 1024.98px) {
          .nav__sub {
            left: 0%;
            top: 141px;
            bottom: 164px;
            box-shadow: none;
            .nav__back {
              display: block;
              padding: 12px 10px;
              color: var(--ink-70);
              font-size: 1.6rem;
              text-align: right;
              background-color: var(--ink-30);
              color: var(--white);
              span:nth-child(2) {
                margin-left: 10px;
              }
            }
          }
        }
        @media (max-width: 568px) {
          .nav__sub {
            top: 45px;
          }
        }
        @media (min-width: 1025px) {
          position: relative;
        }
      }
      @media (max-width: 1024.98px) {
        &.active {
          &:hover {
            .nav__sub {
              opacity: 1;
              visibility: visible;
              transform: translateY(0);
            }
          }
        }
      }
      @media (min-width: 1025px) {
        &:hover {
          .nav__sub {
            opacity: 1;
            visibility: visible;
            transform: translateY(0);
          }
        }
      }
      .nav__link {
        span.fal {
          margin-left: 10px;
        }
      }
    }
  }
}
