.container-fluid,
.container {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  padding-right: 10px;
  padding-left: 10px;
  @media (min-width: 1025px) {
    padding-right: 15px;
    padding-left: 15px;
  }
}
.container {
  @media (min-width: 1025px) {
    max-width: 970px;
  }
  @media (min-width: 1200px) {
    max-width: 1150px;
  }
  @media (min-width: 1440px) {
    max-width: 1280px;
  }
  @media (min-width: 1660px) {
    max-width: 1280px;
  }
}
.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -10px;
  margin-left: -10px;
  @media (min-width: 1025px) {
    margin-left: -15px;
    margin-right: -15px;
  }
}

.col,
[class*="col-"] {
  width: 100%;
  padding-right: 10px;
  padding-left: 10px;
  @media (min-width: 1025px) {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.no-gutters {
  margin-right: 0;
  margin-left: 0;
}
.no-gutters > .col,
.no-gutters > [class*="col-"] {
  padding-right: 0;
  padding-left: 0;
}

